<template>
    <div class="monitoring">
        <vx-card>
            <div class="w-full">
                <vs-table :data="monitoring">
                    <template slot="thead" >
                        <vs-th sort-key="nama" class="bg-primary text-white">
                            GERAI
                        </vs-th>
                        <vs-th sort-key="mulai_aktif" class="bg-primary text-white date">
                            MULAI POS
                        </vs-th>
                        <vs-th sort-key="data_terakhir" class="bg-primary text-white date">
                            DATA TERAKHIR
                        </vs-th>
                    </template>
        
                    <template slot-scope="{data}">
                    <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data">
                        <vs-td :data="data[indextr].nama">
                        {{data[indextr].nama}}
                        </vs-td>
        
                        <vs-td :data="data[indextr].mulai_aktif">
                        {{data[indextr].mulai_aktif}}
                        </vs-td>
        
                        <vs-td :data="data[indextr].data_terakhir" >
                        {{data[indextr].data_terakhir}}
                        </vs-td>
                    </vs-tr>
                    </template>
                </vs-table>
            </div>
        </vx-card>
    </div>
</template>

<script>
export default {
   data: function() {
    return {
        monitoring: [],
    }
   },

   mounted() {
    this.loadMonitoring()
   },

   methods: {
    loadMonitoring() {
        this.$vs.loading()
        this.$http.post('/pos/toko/monitoringStatus')
          .then(response => {
            if(response.data.status == "SUKSES"){
                // console.log(response.data.data);
                this.monitoring = response.data.data
            }
        })
        .catch(error => {error})
        setTimeout( ()=> {
            this.$vs.loading.close()
        }, 1000);
    }
   }
}
</script>

<style scoped>
.date {
    width: 180px;
}
</style>